import React from 'react'
import Data from '../components/Data';
import Project from '../components/Project';
import { Button } from '@mui/material'
import { motion } from "framer-motion";

export default function Projects(items) {

  let Autocard = Data.map(items => {
    return(
        <Project
          name={items.title}
          tech={items.stack}
          link={items.url}
          gthub={items.prolink}
        />
      
    )
  })


  return (
    <motion.div
            initial={{ 
          y: 600,
          opacity: 0 }}
      animate={{ opacity: 1, 
        y: 0,
    transition: {
      delay:0.2,
      duration: 0.4,
      },
      }}
      exit={{ 
        opacity: 0,
      }} id='projects-bg' className='color2'>
    <h1> rojects</h1>
    {Autocard}
    <div className='centered' >
    <Button 
    href="https://faizshariff.github.io/Contact/"
    sx={{
      fontWeight:'700',
      marginTop:'-20vh'
    }}
    className='btn' variant="contained">Next</Button>
    </div>
    </motion.div>
  )
}
